'use strict'

/*
 * Convert string to ArrayBuffer
 */
export function str2ab(str) {
  const buf = new ArrayBuffer(str.length)
  const bufView = new Uint8Array(buf)
  for (let i=0, strLen=str.length; i<strLen; i++) {
    bufView[i] = str.charCodeAt(i)
  }
  return buf
}

function getPrivateBinaryDER(pem) {
  // fetch the part of PEM string between header and footer
  const pemHeader = '-----BEGIN PRIVATE KEY-----\n'
  const pemFooter = '-----END PRIVATE KEY-----\n'
  const pemContents = (
    pem.substring(pemHeader.length, pem.length - pemFooter.length)
  )
  const pemContentsStripped = pemContents.replace(/\n/gi, '')
  const binaryDerString = window.atob(pemContentsStripped)
  // Convert a binary string to an ArrayBuffer
  return str2ab(binaryDerString)
}

/*
 * Import RSA private key in PKCS-8 PEM format for signing purpose
 */
export async function importSigningPrivateKey(pem) {
  const binaryDer = getPrivateBinaryDER(pem)
  try {
    return await crypto.subtle.importKey(
      'pkcs8',
      binaryDer,
      {
        name: 'RSA-PSS',
        modulusLength: 2048,
        publicExponent: new Uint8Array([1, 0, 1]),
        hash: 'SHA-256'
      },
      true,
      ['sign']
    )
  } catch(e) {
    console.debug("Error:", e)
    return null
  }
}

/*
 * Import RSA private key in PKCS-8 PEM format for decryption purpose
 */
export async function importDecryptionPrivateKey(pem) {
  const binaryDer = getPrivateBinaryDER(pem)
  try {
    return await crypto.subtle.importKey(
      'pkcs8',
      binaryDer,
      {
        name: 'RSA-OAEP',
        modulusLength: 2048,
        publicExponent: new Uint8Array([1, 0, 1]),
        hash: 'SHA-256'
      },
      true,
      ['decrypt']
    )
  } catch(e) {
    console.debug("Error:", e)
    return null
  }
}

export async function importPublicKey(pem) {
  // fetch the part of PEM string between header and footer
  const pemHeader = '-----BEGIN PUBLIC KEY-----\n'
  const pemFooter = '-----END PUBLIC KEY-----\n'
  const pemContents = (
    pem.substring(pemHeader.length, pem.length - pemFooter.length)
  )
  const pemContentsStripped = pemContents.replace(/\n/gi, '')
  const binaryDerString = window.atob(pemContentsStripped)
  // Convert a binary string to an ArrayBuffer
  const binaryDer = str2ab(binaryDerString)
  try {
    return await crypto.subtle.importKey(
      'spki',
      binaryDer,
      { name: 'RSA-OAEP', hash: 'SHA-256' },
      true,
      ['encrypt']
    )
  } catch(e) {
    console.debug("Error:", e)
    return null
  }
}

/*
 * Certificate consist of two parts (separated by dot):
 * - expiration time (seconds since epoch in UTC)
 * - RSA signature
 */
export async function generateCertificate(privateKey, serialNumber, expireDays) {
  let expire = Math.round(Date.now()/1000) + (86400*expireDays)
  let stringToSign = `${serialNumber}.${expire}`
  let enc = new TextEncoder()
  let encodedData = enc.encode(stringToSign)

  let signature = await crypto.subtle.sign(
    { name: 'RSA-PSS', saltLength: 32 },
    privateKey,
    encodedData
  )

  // Convert to bytes then encode using base64 encoding scheme
  let signatureB64 = window.btoa(
    String.fromCharCode.apply(null, new Uint8Array(signature))
  )

  let expirePart = window.btoa(expire).replace(/=/gi, '')
  let signaturePart = signatureB64.replace(/=/gi, '')
  return `${expirePart}.${signaturePart}`
}

export async function encryptMessage(key, message) {
  let enc = new TextEncoder()
  let encodedData = enc.encode(message)
  try {
    let ciphertext = await crypto.subtle.encrypt(
      { name: 'RSA-OAEP' },
      key,
      encodedData
    )
    return window.btoa(
      String.fromCharCode.apply(null, new Uint8Array(ciphertext))
    )
  } catch(e) {
    console.error('Error:', e)
    return ''
  }
}

export async function decryptMessage(key, encryptedB64) {
  let ciphertext = str2ab(window.atob(encryptedB64))
  try {
    let d = await crypto.subtle.decrypt(
      { name: 'RSA-OAEP' },
      key,
      ciphertext
    )
    return String.fromCharCode.apply(null, new Uint8Array(d))
  } catch(e) {
    console.error('Error:', e)
    return ''
  }
}
