'use strict'

import Vue from 'vue'
import VueRouter from 'vue-router'

import Signer from '../views/Signer.vue'
import Encrypt from '../views/Encrypt.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'signer',
    component: Signer
  },
  {
    path: '/encrypt',
    name: 'encrypt',
    component: Encrypt
  },
]

const router = new VueRouter({ routes })

export default router
