<template>
  <div>
    <h1>Certificate Signer</h1>
    <div>
      <div>RSA private key (PKCS-8 format):</div>
      <textarea v-model="privateKeyPEM"></textarea>
    </div>
    
    <div v-if="privateKeyPEM.length > 0">
      <div>Encrypted message:</div>
      <textarea class="encmsg" v-model="encryptedMessage"></textarea>
    </div>

    <div v-if="encryptedMessage.length > 0">
      Expires in: <input type="number" v-model.number="expireDays" /> days
    </div>
    <div v-if="encryptedMessage.length > 0">
      <button @click="expireDays = 365">1 year</button>
      <button @click="expireDays = 365 * 5">5 year</button>
      <button @click="expireDays = 365 * 10">10 year</button>
      <button @click="expireDays = 365 * 30">30 year</button>
    </div>
    <div>
      <button
        @click="genCertificate"
        v-if="encryptedMessage.length > 0"
      >Generate certificate</button>
    </div>
    <div v-if="encryptedMessage.length > 0">
      <h3 v-if="certificate.length > 0">Certificate:</h3>
      <div class="certificate" ref="certificate"
      >{{ certificate }}</div>
      <div>
        <button @click="copyToClipboard"
          v-if="certificate.length > 0"
        >Copy to clipboard</button>
      </div>

      <div v-if="message!==''">
        <h3>Log data:</h3>
        <div>
          <span ref="logData">{{ logData }}</span>
          <button @click="copyLogData">Copy</button>
        </div>
      </div>
    </div>

    <div class="bottom-nav">
      <router-link to="/encrypt">Encryption page</router-link>
    </div>
  </div>
</template>

<script>
'use strict'

import { decryptMessage, generateCertificate, importSigningPrivateKey,
  importDecryptionPrivateKey } from '../lib/crypto_utils.js'
import { copyToClipboard, decodeExpire } from '../lib/utils.js'

//~ import { privateKeyPEM } from '../lib/mock.js'

const DEFAULT_EXPIRE_DAYS = 14

export default {
  name: 'Signer',
  data: () => ({
    privateKeyPEM: '',
    signingPrivateKey: null,
    decryptionPrivateKey: null,
    message: '',
    encryptedMessage: '',
    expireDays: DEFAULT_EXPIRE_DAYS,
    certificate: '',
    logData: ''
  }),
  created: async function () {
    //~ this.privateKeyPEM = privateKeyPEM
  },
  methods: {
    genCertificate: async function() {
      if (this.decryptionPrivateKey === null) {
        this.decryptionPrivateKey = await importDecryptionPrivateKey(this.privateKeyPEM)
      }
      if (this.signingPrivateKey === null) {
        this.signingPrivateKey = await importSigningPrivateKey(this.privateKeyPEM)
      }
      this.message = await decryptMessage(
        this.decryptionPrivateKey, this.encryptedMessage
      )
      if ('' === this.message) return

      this.certificate = await generateCertificate(
        this.signingPrivateKey, this.message, this.expireDays
      )
      
      let nowTs = Math.round(Date.now()/1000)
      let d = {
        message: this.message,
        created: nowTs,
        expire: decodeExpire(this.certificate.split('.')[0])
      }
      this.logData  = JSON.stringify(d)
    },
    copyToClipboard() {
      let success = copyToClipboard(this.$refs.certificate)
      if (success) {
        alert("Certificate has been copied to clipboard")
      }
    },
    copyLogData() {
      copyToClipboard(this.$refs.logData)
    },
  }
}
</script>

<style scoped>
textarea {
  width: 576px;
  height: 128px;
}

.encmsg {
  height: 96px;
}

.certificate {
  overflow-wrap: anywhere;
}
</style>