<template>
  <div>
    <h1>Encrypt Message</h1>
    
    <div>RSA public key</div>
    <textarea v-model="publicKeyPEM"></textarea>
    
    <div>
      Message: <input v-model="message" />
    </div>
    
    <div v-if="message.length > 0">
      <button @click="encrypt">Encrypt</button>
    </div>
    <div class="encrypted" ref="encrypted">{{ encryptedMessage }}</div>
    <div>
      <button @click="copyToClipboard"
        v-if="encryptedMessage.length > 0"
      >Copy To Clipboard</button>
    </div>

    <div class="bottom-nav">
      <router-link to="/">Home</router-link>
    </div>
  </div>
</template>

<script>
"use strict"

import { encryptMessage, importPublicKey } from '../lib/crypto_utils.js'
import { copyToClipboard } from '../lib/utils.js'

//~ import { publicKeyPEM } from '../lib/mock.js'

export default {
  name: "Encrypt",
  data: () => ({
    publicKeyPEM: '',
    publicKey: null,
    message: '', // device's serial number
    encryptedMessage: '',

  }),
  created() {
    //~ this.publicKeyPEM = publicKeyPEM
  },
  methods: {
    encrypt: async function() {
      if (null === this.publicKey) {
        this.publicKey = await importPublicKey(this.publicKeyPEM)
      }
      
      this.encryptedMessage = await encryptMessage(this.publicKey, this.message)
    },
    copyToClipboard() {
      let success = copyToClipboard(this.$refs.encrypted)
      if (success) {
        alert("Encrypted message has been copied to clipboard")
      }
    }
  }
}
</script>

<style scoped>
textarea {
  width: 576px;
  height: 144px;
}

.encrypted {
  overflow-wrap: anywhere;
}
</style>
